<script>
import carousel from 'vue-owl-carousel'

export default {
  name: 'home',
  data () {
  	return {
  		count_scroll: 4,
  		carousel_margin: 25
  	}
  },
  components: { carousel },
  beforeMount() {
  	if($(window).width() <= 375) {
  		this.count_scroll = 2
  		this.carousel_margin = 10
  	}
  	else if($(window).width() <= 427) {
  		this.count_scroll = 2
  		 this.carousel_margin = 10
  	}
  	else if($(window).width() <= 768) {
  		this.count_scroll = 3;
  		this.carousel_margin = 10
  	}
  	else if($(window).width() <= 991) {
  		this.carousel_margin = 10
  	}
  },
  mounted () {
  	var lastScrollTop = 0;
		$(window).scroll(function(event){
		   var st = $(this).scrollTop();
		   if (st > lastScrollTop){
		   		$('.div-navbar-brand-mini img').addClass('hide-navbar')
		   		$('.div-navbar-brand-mini ').addClass('hide-navbar')
		   		$('.app-navbar ').addClass('hide-navbar')
		   } else {
		   		$('.div-navbar-brand-mini img').removeClass('hide-navbar')
		   		$('.div-navbar-brand-mini ').removeClass('hide-navbar')
		   		$('.app-navbar ').removeClass('hide-navbar')
		   }
		   lastScrollTop = st;
		});
	}
}
</script>

